<template><section>
<h2>Message box </h2>
<p></p>
</section></template>

<script>
  export default {
    methods: {
      open() {
        this.$alert('', '');
      },

      open2() {
        this.$confirm(', ?', '', {
          type: 'warning'
        }).then(() => {
          this.$message({
            message: '!',
            type: 'success'
          });
        }).catch(() => {
          this.$message({
            message: '',
            type: 'info'
          });          
        });
      },

      open3() {
        this.$prompt('', '', {
          inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
          inputErrorMessage: ''
        }).then(({ value }) => {
          this.$message({
            type: 'success',
            message: ': ' + value
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });       
        });
      },


      open4() {
        this.$msgbox({
          title: '',
          message: ', , , , , , ',
          showCancelButton: true
        }).then(action => {
          this.$message({
            type: 'info',
            message: 'action: ' + action
          });
        });
      }

    }
  };
</script>


