<template><section>

<h2>Notification </h2>
</section></template>
<style>
  .demo-box.demo-notification {
    .el-button + .el-button {
      margin-left: 10px;
    }
  }
</style>
<script>
  module.exports = {
    methods: {
      open() {
        this.$notify({
          title: '',
          message: ''
        });
      },

      open2() {
        this.$notify({
          title: '',
          message: '',
          duration: 0
        });
      },

      open3() {
        this.$notify({
          title: '',
          message: '',
          type: 'success'
        });
      },

      open4() {
        this.$notify({
          title: '',
          message: '',
          type: 'warning'
        });
      },

      open5() {
        this.$notify({
          title: '',
          message: '',
          type: 'info'
        });
      },

      open6() {
        this.$notify({
          title: '',
          message: '',
          type: 'error'
        });
      },

      onClose() {
        console.log('Notification ');
      }
    }
  };
</script>


