<template><section>

<h2>Message </h2>
<p> Notification </p>
</section></template>
<style>
  .demo-box.demo-message {
    .el-button + .el-button {
      margin-left: 10px;
    }
  }
</style>
<script>
  module.exports = {
    methods: {
      open() {
        this.$message('');
      },

      open2() {
        this.$message({
          message: '',
          type: 'success'
        });
      },

      open3() {
        this.$message({
          message: '',
          type: 'warning'
        });
      },

      open4() {
        this.$message({
          message: '',
          type: 'error'
        });
      },

      open5() {
        this.$message({
          showClose: true,
          message: ''
        });
      },

      open6() {
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
      },

      open7() {
        this.$message({
          showClose: true,
          message: '',
          type: 'warning'
        });
      },

      open8() {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      }
    }
  };
</script>


