<template><section>
<h2>Steps </h2>
<p> 2 </p>
</section></template>

<script>
  export default {
    data() {
      return {
        active: 0
      };
    },

    methods: {
      next() {
        if (this.active++ > 2) this.active = 0;
      }
    }
  }
</script>


